import React from "react";
import Stack from "@mui/material/Stack";
import InputLabel from "@mui/material/InputLabel";
import FormControl from "@mui/material/FormControl";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import Container from "@mui/material/Container";
import { ModalNotification, TopNav } from "../../components";
import { useNavigate } from "react-router-dom";
import makeStyles from "@mui/styles/makeStyles";
import { connect } from "react-redux";
import { login } from "../../redux/actions/auth";

const useStyles = makeStyles(() => ({
  root: {
    paddingTop: "16px",
  },
}));
const Login = (props) => {
  const { actLogin } = props;
  const classes = useStyles();
  const [type, setType] = React.useState("");
  const [typeVal, setTypeVal] = React.useState("Nomer MR");
  const [openSucc, setOpenSucc] = React.useState(false);
  const [openFail, setOpenFail] = React.useState(false);
  const handleType = (e) => {
    setType("");
    setTypeVal(e.target.value);
  };
  const changeType = (e) => {
    let value = e.target.value;
    if (typeVal === "Nomer MR") {
      let str = value.replace(/\D/g, "").match(/(\d{0,2})(\d{0,2})(\d{0,2})/);
      str = !str[2]
        ? str[1]
        : str[1] + "-" + str[2] + (str[2].length > 1 ? "-" : "") + str[3];
      setType(str);
    } else if (typeVal === "Nomer Tlp") {
      let str = value.replace(/\D/g, "").match(/(\d{0,4})(\d{0,4})(\d{0,4})/);
      str = !str[2]
        ? str[1]
        : str[1] + "-" + str[2] + (str[2].length > 3 ? "-" : "") + str[3];
      setType(str);
    } else {
      setType(e.target.value);
    }
  };
  const navigate = useNavigate();
  return (
    <>
      {/* <Button onClick={() => setOpen(true)}>Open</Button> */}
      {/* <ModalNotification open={openSucc} setOpen={setOpenSucc} /> */}
      <ModalNotification
        open={openFail}
        setOpen={setOpenFail}
        title={"Failed"}
        subtitle={"Gagal banget sekali"}
      />
      <Container className={classes.root}>
        <Stack spacing={2}>
          <TopNav type="back" title="Login" click={() => navigate(-1)} />
          <FormControl fullWidth>
            <InputLabel id="type">Type</InputLabel>
            <Select
              labelId="type"
              id="demo-simple-select"
              value={typeVal}
              label="Type"
              onChange={handleType}
            >
              <MenuItem value={"Nomer MR"}>No MR</MenuItem>
              <MenuItem value={"Nomer Tlp"}>Telp</MenuItem>
              <MenuItem value={"Email"}>Email</MenuItem>
            </Select>
          </FormControl>
          <TextField
            id="type"
            label={typeVal}
            value={type}
            onChange={changeType}
          />
          <TextField
            id="outlined-password-input"
            label="Password"
            type="password"
            autoComplete="current-password"
          />
          <Button
            variant="contained"
            onClick={() => actLogin({}, () => setOpenFail(true))}
            // onClick={() => navigate("/")}
          >
            Login
          </Button>
        </Stack>
      </Container>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    // stateEmail: state.auth.email,
    // stateCompany: state.app.company,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    actLogin: (body, action) => dispatch(login(body, action)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Login);
